.responsive-visibility() {
  display: inline;
}

.responsive-invisibility() {
  display: hidden;
}

.visible-sm {
  .responsive-visibility();

  @media (min-width: @screen-tablet) {
    .responsive-invisibility();
  }

  @media (min-width: @screen-desktop) {
    .responsive-invisibility();
  }
}

.visible-md {
  .responsive-invisibility();

  @media (min-width: @screen-tablet) {
    .responsive-visibility();
  }

  @media (min-width: @screen-desktop) {
    .responsive-invisibility();
  }
}

