.clearfix() {

  &::after {
    display: block;
    content: '';
    clear: both;
  }
}

.placeholder-form (@rules) {

  &::-webkit-input-placeholder {

    @rules();
  }

  &:-moz-placeholder {

    @rules();
  }

  &::-moz-placeholder {

    @rules();
  }

  &:-ms-input-placeholder {

    @rules();
  }

  &:focus::-webkit-input-placeholder {
    color: transparent;
  }

  &:focus:-moz-placeholder {
    color: transparent;
  }

  &:focus::-moz-placeholder {
    color: transparent;
  }

  &:focus:-ms-input-placeholder {
    color: transparent;
  }
}

input:focus {
  border: 1px solid @gray;
}